import { useEffect, useState } from 'react';
import './App.css';
import { gameSubject, initGame, resetgame } from './model/Game';
import ChessState from './context/ChessState';
import Board from './model/Board';
function App() {
  const [board, setboard] = useState([])
  const [isGameOver, setisGameOver] = useState()
  const [result, setresult] = useState()
  const [turn, setturn] = useState()


  useEffect(() => {
    initGame()
    const subscribe = gameSubject.subscribe((game) => {
      setboard(game.board)
      setisGameOver(game.isGameOver)
      setresult(game.result)
      setturn(game.turn)
    })


    return () => subscribe.unsubscribe()
  }, [])
  return (
    <ChessState>
    <h1 style={{textAlign:"center"}}>WELCOME TO KB-Chess</h1>
    <div className='container'>
      {isGameOver && <h2 className='vertical-heading'>
        GAME OVER
        <button onClick={()=>{resetgame()}}>
          <span   className="vertical-heading">
            New Game
          </span>
        </button>
      </h2>
      }

      <div className='board-container'>
        <Board board={board} turn={turn} />
      </div>
      {result && <p className='vertical-heading'>{result}</p>}
    </div>
    </ChessState>
  );
}

export default App;
